import React from "react";
import PopupWithForm from "./PopupWithForm";

export default function DelPlacePopup(props) {
  const { isOpen, onClose, onDelPlace, isLoading } = props;

  const handleSubmit = (evt) => {
    evt.preventDefault();
    onDelPlace();
  };

  return (
    <PopupWithForm
      title=" ?"
      name="del"
      subTitle={isLoading ? "..." : ""}
      isOpen={isOpen}
      onClose={onClose}
      isValid={true}
      onSubmit={handleSubmit}
    />
  );
}



